












































export default {
    layout: 'AdminLayout',
    data() {
      return {
        dialog: false,
        delemail: '',
      }
    },
    fetch({ store }) {
      return store.dispatch('admin/loadUser');
    },
    computed: {
      users() {
        return this.$store.state.admin.allUser;
      }
    },
    methods: {
      update() {
        this.$store.dispatch('admin/loadUser');
      },
      async deleteUser(email) {
        const deleteResponse = await this.$store.dispatch('admin/deleteUser', email);
        this.dialog = false;
      },
      choice(email) {
        this.delemail = email;
        this.dialog = true;
      }
    }
}
